<template>
  <div>
    <Heads :title="title" />
    <section id="section">
     
       <!--   -->
       <div class="type">
            <h3 style="margin:10px"> </h3>
            <v-sheet
              v-model="model"
              class="mx-auto"
              max-width="100%"
            >
              <v-slide-group
                v-model="model"
                show-arrows
              >
                <v-slide-item
                  v-for="n in 7"
                  :key="n"
                  v-slot="{ active, toggle }"
                >
                  <v-btn
                    name="meal"
                    width="10px"
                    :input-value="active"
                    active-class="purple white--text"
                    depressed
                    rounded
                    @click="toggle"
                  >
                    {{ meal[n-1] }}
                  </v-btn>
                </v-slide-item>
              </v-slide-group>
            </v-sheet>
          </div>
          <!--  -->
          <div class="input1">
            <router-link to="/gandanView" style="text-decoration:none; ">
          <v-btn class="inputbtn" > </v-btn>
          </router-link>
          <router-link to="/detailInputView" style="text-decoration:none; ">
          <v-btn class="inputbtn" color="grey darken-2" dark> </v-btn>
          </router-link>
          </div>
      <div class="inner">
        <div class="photo">
          <label for="chooseFile">
            <div class="addImg" id="addImg"></div>
          </label>
          <div id="image-show"></div>
          <input
            type="file"
            id="chooseFile"
            name="chooseFile"
            accept="image/*"
            onchange="loadFile(this)"
            required
            multiple
          />
        </div>
      </div>
      <div class="menu inner">
        <ul>
          <li>  :(</li>
        </ul>
      </div>
      <div class="food-wrapper inner">
        <h3> </h3>
        <div class="food-search">
          <div class="food-img"></div>
          <input type="text" />
        </div>
        <table class="food-table">
          <tr>
            <th></th>
            <th> </th>
            <th> </th>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td>300kcal</td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td>51kcal</td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td>kcal</td>
          </tr>
          <tr>
            <td></td>
            <td> </td>
            <td></td>
          </tr>
        </table>
        <button class="food-btn"></button>
      </div>
    </section>
  </div>
</template>

<script>

import Heads from "../components/header.vue";
export default {
  mounted() {
    
     // eslint-disable-next-line no-undef
     jQuery(".tab-content > li").off("click").click(function () {
          // eslint-disable-next-line no-undef
          jQuery(".tab-content > li").removeClass("active");
          // eslint-disable-next-line no-undef
          jQuery(this).addClass("active");

          // eslint-disable-next-line no-undef
          var offset = jQuery(this).offset().left - 15;
          // eslint-disable-next-line no-undef
          jQuery(".tab-scroll-bar").animate({ left: offset });
        });



  },
  name: "detailView",
  data() {
    return {
      title : ' ',
      meal:['','','','','','',''],
    };
  },
  components: {
    Heads: Heads,
  },
};
</script>

<style>
@import url("../assets/css/style.css");

.type{
      margin-top:10px;
      margin-bottom:15px;
    }
</style>
